import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Box, Container, Typography } from '@mui/material'
import { Card } from '@mui/material'

import type { ConferenceCategory } from 'src/data/conferenceCategories'
import { COMMON_MESSAGE, CONFERENCE_MESSAGE } from 'src/data/messages'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import ConferenceInfo from 'src/content/manage/Conference/ConferenceEdit/ConferenceInfo'
import PageHeader from 'src/content/manage/PageHeader'

import { useEditConference, useGetSingleConference } from 'src/hooks/api/clubConferenceHooks'
import { useGetMembers } from 'src/hooks/api/member'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

function ConferenceEdit() {
  const { enqueueSnackbar } = useSnackbar()
  const { userInfo } = useSafeAuthUser()
  const { id, confId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)

  const club = userInfo?.clubs.find(club => club.id === clubId)

  const { data: members } = useGetMembers(clubId)
  const { data, isSuccess } = useGetSingleConference(clubId, conferenceId)
  const editConferenceMutation = useEditConference()

  const [title, setTitle] = useState<string>('')
  const [selectedMembers, setSelectedMembers] = useState<number[]>([])
  const [participantTarget, setparticipantTarget] = useState<string>('')
  const [categories, setCategories] = useState<ConferenceCategory[]>([])

  useEffect(() => {
    if (isSuccess && data && members) {
      setTitle(data.title)
      setparticipantTarget(data.participantTarget ?? '')
      setSelectedMembers(data?.conferenceParticipants?.map(member => member.user.id) ?? [])
      setCategories(data.categories || [])
    }
  }, [isSuccess, data, members])

  const changeTitle = (title: string) => {
    setTitle(title)
  }

  const changeParticipantTarget = (participantTarget: string) => {
    setparticipantTarget(participantTarget)
  }

  const handleSubmit = async () => {
    if (title.length > 50) {
      enqueueSnackbar(CONFERENCE_MESSAGE.TITLE_LIMIT, { variant: 'error' })
      return
    }

    if (isSuccess) {
      editConferenceMutation.mutate({
        clubId,
        conferenceId: data.id,
        participantTarget,
        title,
        users: selectedMembers,
        categories: categories
      })
      return
    }
    enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
    return
  }

  return (
    <>
      <Helmet>
        <title>회의 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} buttonText="행사 추가" link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container>
        <Card>
          <Box m={3}>
            <Typography variant="h4" component="h4">
              참여자 및 회의 정보 수정
            </Typography>
            <ConferenceInfo
              title={title}
              changeTitle={changeTitle}
              participantTarget={participantTarget}
              changeParticipantTarget={changeParticipantTarget}
              members={members ?? []}
              selectedMembers={selectedMembers}
              setSelectedMembers={setSelectedMembers}
              categories={categories}
              changeCategories={setCategories}
              handleSubmit={handleSubmit}
            />
          </Box>
        </Card>
      </Container>
    </>
  )
}

export default ConferenceEdit
